/** @NgInject */
function DelijnService($http) {
  this.getNextBusses = function (callback) {
    return $http({
      method: 'GET',
      url: 'https://www.delijn.be/rise-api-core/haltes/doorkomstenditmoment/105458/13'
    })
  };
}

angular
  .module('app')
  .service('ApiService', ApiService);

/** ngInject */
function ApiService($http, $log) {
  var svc = this;

  const baseUrl = 'http://planni.app/api/';

  svc.getMessages = function (eventId) {
    return $http.get(baseUrl + 'messages');
  }
}

angular.module('app')
  .controller('MessagesController', MessagesController);

/** @NgInject */
function MessagesController($log, ApiService) {

  var vm = this;

  vm.messages = [];

  function _init() {
    $log.log("MessagesController loaded!");

    ApiService.getMessages().then(
      function onSucces(response) {
        $log.debug(response);
        vm.messages = response.data;
      },
      function onError(response) {
        $log.error("Getting messages failed:", response);
      }
    );
  }
  _init();

}

