angular
  .module('app')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('app', {
      url: '/',
      template: '<ui-view/>',
      controller: 'MainController as main'
    })
    .state('app.weather', {
      url: 'weather',
      templateUrl: 'app/weather/weather.html',
      controller: 'WeatherController as weather'
    })
    .state('app.transport', {
      url: 'transport',
      templateUrl: 'app/transport/transport.html',
      controller: 'TransportController as transport'
    })
    .state('app.messages', {
      url: 'messages',
      templateUrl: 'app/messages/messages.html',
      controller: 'MessagesController as messages'
    });
}

angular
  .module('app')
  .controller('MainController', MainController);

/** @ngInject */
function MainController($timeout, $state, $log) {
  var currentSlideId = 0;
  var slides = [
    {
      name: 'weather',
      duration: 5000
    },
    {
      name: 'transport',
      duration: 2000
    }
  ];

  function nextSlide() {
    currentSlideId = (currentSlideId + 1 === slides.length ? 0 : currentSlideId + 1);
    $state.go("app." + slides[currentSlideId].name);
    $timeout(nextSlide, slides[currentSlideId].duration);
  }

  function _init() {
    $log.debug('MainController loaded!');
    //nextSlide();
  }
  _init();
}

